import APIBuilder from "./_api";

const API_URL = process.env.REACT_APP_GATEWAY;

class UsersApi extends APIBuilder {
    constructor() {
        debugger;
        super({
            baseURL: API_URL,
        });
    }

    getFoo() {
        return this.get("/data/users.json")
            .then((response) => {
                debugger;
                return response.json();
            })
            .catch((e) => {
                throw new Error(e);
            });
    }
}

export default new UsersApi();
